const state = {
  addPackDialog: false,
  packServiceList: []
}

const getters = {
  getAddPackDialog: state => state.addPackDialog,
  getPackServiceList: state => state.packServiceList
}

const actions = {}

const mutations = {
  
  setPackServiceList(state, list) {
  	state.packServiceList = list
  },

  pushPackServiceList(state, {serviceItem, fail, success}) {
    let canPush = true

    state.packServiceList.forEach((item) => {
      if (item.servId === serviceItem.servId) {
        canPush = false

        if (typeof fail === 'function') {
          fail()	
        }
        
      }
    })

    if (canPush) {
      state.packServiceList.push(serviceItem)
      if (typeof success === 'function') {
      	success()
      }
    }
  }

}

export default {
  state,
  getters,
  mutations,
  actions
}


